import React from "react";



const Header = () => {
    return (
        <div>

            <div className="pic">
                <div className="header">

                    <div className="loaction"> Text for loaction</div>
                    <div className="subWay">
                        <img src="./subwaym.jpg" alt="subway" width="50px" height="50px" />
                    </div>
                    <div className="register">Sig in / register</div>
                    <div className="main2">
                        <div className="quantity">0</div>
                        <div className="cart"><img src="arow.jpg" alt="arow" width="30px" height="30px" /></div>
                        <div className="arow"><img src="./Arrow.jpg" alt="Arow" width="10px" height="10px" /> </div>
                    </div>

                </div>

                <div className="slider">

                    <div id="carouselExampleIndicators" class="carousel slide" data-ride="carousel">
                        <ol class="carousel-indicators">
                            <li data-target="#carouselExampleIndicators" data-slide-to="0" class="active"></li>
                            <li data-target="#carouselExampleIndicators" data-slide-to="1"></li>
                        </ol>
                        <div class="carousel-inner">

                            <div class="carousel-item active">
                                <img class="d-block w-100" src="subway.jpg" alt="First slide" />
                            </div>

                            <div class="carousel-item">
                                <img class="d-block w-100" src="subway2.jpg" alt="Second slide" />
                            </div>

                            <a class="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
                                <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                                <span class="sr-only">Previous</span>
                            </a>
                            <a class="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
                                <span class="carousel-control-next-icon" aria-hidden="true"></span>
                                <span class="sr-only">Next</span>
                            </a>

                        </div>
                    </div>

                </div>

            </div>

            <div className="product">
                <ul className="list">
                    <li className="fProduct">6 INCH</li>
                    <li>CHOTU</li>
                    <li>DRINKS</li>
                    <li>FOOT LONG</li>
                    <li>FOUNTAIN DRINKS</li>
                    <li>PLATTER</li>
                    <li>ADD ON</li>
                    <li>DEALS</li>
                    <li>COMBO</li>
                    <li>SALAD</li>
                </ul>
            </div>
            {/* <div className="check"></div> */}
            
        </div>
    )
}

export default Header;