import React from 'react';

const Menu = () => {
    return (

        <div className="Container">
            <div className="searchBar">
                <div className="fBorder">
                    <div className="searchIcon">
                        <img src="search.jpg" alt="search" />
                    </div>
                    <div className="searchText">
                        <input type="text"></input>
                    </div>
                </div>
            </div>

        </div>

    )
}

export default Menu;
